/*-------------------------*/
/* 目錄清單
---------------------------*/

.listDirectory{
  @media screen and (min-width: $mq375) {
    padding-left: 4rem;
    padding-right: 4rem;
  }
  @media screen and (min-width: $mq768) {
    padding:0;
  }
  @media screen and (min-width: $mq1200) {
    padding-top: 2.5rem;
  }
  // li
  &__list{
    margin-bottom: 2rem;
    background-color: $thirdly;
    border-radius: 3.5rem;
    height: 4.8rem;
    position: relative;
    cursor: pointer;
    @media screen and (min-width: $mq768) {
      width: 27rem;
    }
    @media screen and (min-width: $mq1200) {
      @include width(35rem , 7rem);
      margin-bottom: 4.5rem;
    }
  }
  // href
  &__link{
    color: $black;
    text-align: center;
    margin-left: 1rem;
    font-weight: $font-weight-regular;
    @media screen and (min-width: $mq768) {
      padding: 0rem 1rem;
      margin-left: 2rem;
    }
    @media screen and (min-width: $mq1200) {
      @include fontSize(2.2rem , 1.7);
    }
  }
  &__link02{
    margin-left: 2rem;
    @media screen and (min-width: $mq768) {
      margin-left: 3rem;
    }
    @media screen and (min-width: $mq1200) {
      margin-left: 3.5rem;
    }
  }
  &__link03{
    margin-left: 2rem;
    line-height: 1.3;
    @media screen and (min-width: $mq768) {
      margin-left: 3rem;
    }
    @media screen and (min-width: $mq1200) {
      margin-left: 3.5rem;
    }
  }
  &__link:hover{
    color: $primary;
  }
  // 圖片
  &__icon{
    @include width(4.8rem , 4.8rem);
    position: absolute;
    top: 0;
    left: 0;
    @media screen and (min-width: $mq1200) {
      @include width(7rem , 7rem);
    }
  }
}
