/* noticeText
---------------------------*/
.noticeText{
  width:100%;
  max-width: 96rem;
  background-color: $white;
  margin: 0 auto;
  margin-bottom: 2rem; 
  @media screen and (min-width: $mq768) {
    // padding-left: 2.4rem;
    // padding-right: 2.4rem;
    margin-bottom: 5rem; 
  }
  @media screen and (min-width: $mq1024) {
    // margin-bottom: 4.5rem; 
  }
  @media screen and (min-width: $mq1200) {
    max-width: 112rem;
  }
  // 說明
  h4{
    @include fontSize(2rem , 1.7);
    font-weight: 400;
    text-align: left;
    padding-left: 1rem;
    margin-bottom: 1rem;
    @media screen and (min-width: $mq768) {
      @include fontSize(2.2rem , 3.6rem);
    }
    strong{
      color: $primary;
      font-weight: 500;
    }
  }
}
